// @flow
import { clamp } from "../../util/util";
import { number as interpolate } from "../../style-spec/util/interpolate";
import { METERS_PER_UNIT } from "../proj/Units";
import LngLat from "../geo/lng_lat";

import type Map from "../../ui/map";
import type Transform from "../geo/transform";
import type { TaskID } from "../../util/task_queue";

// ä¸äºå·¥å·æ¹æ³
const util = {
    length(coord1, coord2): number {
        return Math.sqrt(
            Math.pow(coord2[0] - coord1[0], 2),
            Math.pow(coord2[1] - coord1[1], 2)
        );
    },
    lengthArray(coords: Array): number {
        let l = 0;
        coords.reduce((pre, next) => {
            l += this.length(pre, next);
            return next;
        });
        return l;
    },
    calcBearing(coord1: Array, coord2: Array): number {
        if (coord1 && coord2) {
            const angle = Math.atan2(
                coord2[1] - coord1[1],
                coord2[0] - coord1[0]
            );
            return 90 - (angle / Math.PI) * 180;
        }
    },
    scalePoint(scale: number, coord1: Array, coord2: Array): Array {
        scale = clamp(scale, 0, 1);
        return coord1.map((c, i) => c + (coord2[i] - c) * scale);
    },
};

// è·¯å¾è®¡ç®æ§å¶å¨
class PosControl {
    // å¸¸é
    PATH: Array; // å®æ´çè·¯å¾
    LENGTH: number; // å¨é¨çé¿åº¦
    ZOOMRANGE: ?[number, number]; // ç¼©æ¾èå´
    MIN_Z: number;
    MAX_Z: number;

    // åéï¼å¯å¤é¨æ¹åé¨åï¼
    speed: number; // éåº¦
    // éå¤é¨æ¹åé¨å
    preIndex: number; // ä¸ä¸ªç¹çç´¢å¼
    currentPoint: Array; // å½åç¹åæ 
    passLength: number; // å·²ç»ç»è¿çç¹çé¿åº¦ï¼ç¨äºç¾åæ¯çè®¡ç®

    // æè½¬åæ°(å¤ä¸ªç¹)
    rotateOptions: Array;

    constructor(path, speedV, zoomRange) {
        this.PATH = path;
        this.LENGTH = util.lengthArray(path);
        this.ZOOMRANGE = zoomRange;

        const zs = path.map((c) => c[2]).filter((v) => typeof v === "number");
        this.MIN_Z = Math.min(...zs);
        this.MAX_Z = Math.max(...zs);

        this.speed = speedV;
        this.preIndex = 0;
        this.currentPoint = path[0];
        this.passLength = 0;

        this.rotateOptions = [];
    }

    // éåº¦
    getSpeed(): number {
        return this.speed;
    }
    setSpeed(value: number) {
        this.speed = value;
    }
    // ç¾åæ¯
    getPercent(): number {
        return clamp(this.passLength / this.LENGTH, 0, 1) * 100;
    }
    setPercent(value: number) {
        const percent = clamp(value, 0, 100) / 100;

        this.passLength = 0;
        this.calcNextPoint(percent * this.LENGTH, {
            index: 1,
            cPoint: this.PATH[0],
        });
        this.rotateOptions = [];
    }
    // è·åå·²ç»éè¿ççº¿æ®µä¿¡æ¯
    getPassLine(): Array {
        const path = this.PATH.slice(0, this.preIndex + 2);
        path.pop();
        return [...path, [...this.currentPoint]];
    }
    // è·åå½åç¹
    getCurrentPoint(): Array {
        return [...this.currentPoint];
    }
    // è·åæ¹ä½ä¿¡æ¯
    getBearing(): number {
        return util.calcBearing(
            this.currentPoint,
            this.PATH[this.preIndex + 1]
        );
    }
    // è·åå½åç¼©æ¾
    getZoom(cPoint = this.currentPoint): number {
        const currentZ = cPoint[2];
        if (currentZ && this.ZOOMRANGE) {
            return (
                this.ZOOMRANGE[0] +
                ((currentZ - this.MIN_Z) / (this.MAX_Z - this.MIN_Z)) *
                    (this.ZOOMRANGE[1] - this.ZOOMRANGE[0])
            );
        }
    }
    // è®¡ç®ä¸ä¸ä¸ªç¹
    calcNextPoint(
        distance = this.speed,
        { index = this.preIndex + 1, cPoint = this.currentPoint } = {}
    ): void {
        // é»è®¤å è·ç¦»
        this.passLength += distance;

        // éç½®æè½¬åæ°
        this.rotateOptions = [];

        // ç»æç¹
        let resultPoint;

        // eslint-disable-next-line no-constant-condition
        while (true) {
            const nextPoint = this.PATH[index];
            // å¦æå­å¨ä¸ä¸ªç¹
            if (nextPoint) {
                const dis = util.length(cPoint, nextPoint);
                // ç®æ ç¹å¨æå®ä¸¤ç¹ä¸­é´
                if (dis >= distance) {
                    resultPoint = util.scalePoint(
                        distance / dis,
                        cPoint,
                        nextPoint
                    );
                    break;
                } else {
                    // ç®æ ç¹ä¸å¨æå®ä¸¤ç¹ä¸­é´
                    distance -= dis;
                    cPoint = nextPoint;
                    index++;

                    // æ·»å æè½¬ç¹
                    this.rotateOptions.push({
                        point: [...cPoint],
                        bearing: util.calcBearing(cPoint, this.PATH[index]),
                    });
                }
            } else {
                // å¦æä¸å­å¨ä¸ä¸ªç¹ï¼è®¤ä¸ºå°è¾¾çº¿æ®µç»å°¾
                index = this.PATH.length;
                this.passLength = this.LENGTH;
                resultPoint = this.PATH[index - 1];
                break;
            }
        }
        // è®¾ç½®ä¸ä¸ªç¹ç´¢å¼
        this.preIndex = index - 1;
        // è®¾ç½®å½åç¹
        this.currentPoint = resultPoint;
    }
}

/**
 * @description é£è¡è·¯å¾
 */
export default class RouteFly {
    // å°å¾å¯¹è±¡
    _map: Map;

    // åä½
    _units: String;

    // å½åæ­æ¾ç¶æ(0åæ­¢ 1æ­æ¾)
    _status: number;

    // å½åçå¨ç»å¯¹è±¡
    _frameId: ?TaskID;

    // ä½ç½®è®¡ç®æ§å¶å¨
    _control: PosControl;

    // å¤é¨æ´æ°æ¹æ³
    update: Function;

    constructor(path: Array, zoomRange?: Array) {
        // è·¯å¾æææ§å¤æ­
        if (!path || path.length <= 1) {
            throw new Error("flyroute: æ æçé£è¡è½¨è¿¹ï¼");
        }

        // ç¼©æ¾èå´å¤æ­
        if (zoomRange) {
            const [min, max] = zoomRange;
            if (!min || !max || min >= max) {
                throw new Error("æ æçzoomRangeï¼");
            }
        }

        // åæ åä½
        this._units = "degrees";

        // åå§åæååé
        this._status = 0;

        // æ§å¶å¨
        this._control = new PosControl([...path], 0, zoomRange);
    }

    // éåº¦ï¼æ´æ°ï¼
    get Speed(): number {
        return Math.round(
            this._control.getSpeed() / (1 / METERS_PER_UNIT[this._units])
        );
    }
    set Speed(value: number) {
        value = Math.max(0, value);
        this._control.setSpeed(
            Math.round(value) * (1 / METERS_PER_UNIT[this._units])
        );
    }

    // ç¾åæ¯
    get Percent(): number {
        return this._control.getPercent();
    }
    set Percent(value: number) {
        this._control.setPercent(value);
    }

    // è·åå·²ç»éè¿çç¹
    get PathLine(): Array {
        return this._control.getPassLine();
    }

    /**
     * @api
     * æ·»å å°å°å¾å¯¹è±¡
     */
    addTo(map: Map) {
        if (!this._map) {
            this._map = map;
            // åå§ååä½åéåº¦
            this._units = map.units || map.projection.getUnits();
            this.Speed = 1;
        }
        return this;
    }

    /**
     * @api
     * ä»å°å¾å¯¹è±¡ä¸­ç§»é¤
     */
    remove() {
        this.stop();
        delete this.update;
        delete this._control;
        delete this._units;
        delete this._map;
    }

    /**
     * @api
     * æ­æ¾å½æ°
     */
    play() {
        this._status = 1;
        this._update();
        return this;
    }

    /**
     * @api
     * éæ°æ­æ¾
     */
    replay() {
        this.stop();
        this._control.setPercent(0);
        this.play();
    }

    /**
     * @api
     * åæ­¢æ­æ¾
     */
    stop() {
        this._status = 0;
        if (this._frameId) {
            this._map._cancelRenderFrame(this._frameId);
            delete this._frameId;
        }
        return this;
    }

    /**
     * @private
     * æ´æ°å½æ°ï¼å°å¾ææï¼
     */
    _update() {
        if (!this._frameId) {
            this._frameId = this._map._requestRenderFrame(() => {
                // å é¤å¸§ID
                delete this._frameId;

                // åæ­¢ç¶æ
                if (this._status === 0) {
                    return;
                }

                const map = this._map;
                const tr: Transform = map.transform;
                const control = this._control;

                // ç¾åæ¯å°äº100ï¼å³å­å¨å¯æ­æ¾
                if (control.getPercent() < 100) {
                    // è®¡ç®ä¸ä¸ªç¹
                    control.calcNextPoint();

                    // æ·»å æè½¬ä¸­é´å¸§
                    this._frame(
                        (k, { point, bearing }) => {
                            // æè½¬ä¸­å½æ°
                            if (k === 0) {
                                tr.center = LngLat.convert(point);
                            }
                            // æ´æ°bearingè§åº¦
                            tr.bearing = bearing;
                        },
                        () => {
                            const options = {
                                center: control.getCurrentPoint(),
                                bearing: +control.getBearing(),
                                zoom: +control.getZoom(),
                            };

                            // ç¼©æ¾
                            if (isFinite(options.zoom)) {
                                tr.zoom = options.zoom;
                            }
                            // æ¹ä½
                            if (isFinite(options.bearing)) {
                                tr.bearing = options.bearing;
                            }
                            // ä¸­å¿ç¹
                            if (options.center) {
                                tr.center = LngLat.convert(options.center);
                            }

                            // å¤é¨æ´æ°
                            if (this.update) this.update(options.center, this);

                            // ç»§ç»­ä¸ä¸å¸§ç
                            this._update();
                        }
                    );
                } else {
                    this.stop();
                }
            });
        }
    }

    /**
     * è¿åº¦å¸§å½æ°
     * @private
     */
    _frame(frame: () => void, finish: () => void) {
        const map = this._map;
        const control = this._control;
        const rotateOptions = control.rotateOptions || [];

        // å¦æå­å¨å¤ä¸ªæç¹
        if (rotateOptions.length) {
            // ä¸ä¸ªæè½¬ç¹
            const next = () => {
                const rotateOption = rotateOptions.shift();
                let { point, bearing } = rotateOption;

                if (bearing) {
                    const startBearing = map.getBearing();
                    bearing = map._normalizeBearing(bearing, startBearing);
                    const l = Math.floor(Math.abs(bearing - startBearing));

                    let i = 0;
                    // ä¸ä¸ªæè½¬å¸§
                    const nextFrame = () => {
                        const k = i / l;
                        map._requestRenderFrame(() => {
                            frame(k, {
                                point,
                                bearing: interpolate(startBearing, bearing, k),
                            });

                            // å®æåä¸ªæç¹
                            if (k >= 1) {
                                if (rotateOptions.length === 0) {
                                    finish();
                                } else {
                                    next();
                                }
                            } else {
                                i++;
                                nextFrame();
                            }
                        });
                    };
                    nextFrame();
                } else {
                    finish(1);
                }
            };
            // æ§è¡å½æ°
            next();
        } else {
            finish(1);
        }
    }
}
